import { useEffect } from 'react';

import './Landing.scss';

const Landing = () => {
	useEffect(() => {
		const texts = [ 'Full-Stack Developer', 'Engineering Student', 'Sim Racer', 'Human' ];
		let typewriter = document.getElementById('typewriter-text');
		let text_no = 0;
		let text_pos = 0;
		let parseDirection = 0;
		setInterval(() => {
			let text = texts[text_no];
			if (parseDirection === 0) {
				text_pos++;
			} else {
				text_pos--;
			}

			typewriter.innerHTML = text.substring(0, text_pos);
			if (text_pos === text.length && parseDirection === 0) {
				setTimeout(() => {
					parseDirection = 1;
				}, 400);
			} else if (text_pos < 0 && parseDirection === 1) {
				parseDirection = 0;
				text_pos = 0;
				if (text_no < texts.length - 1) {
					text_no++;
				} else {
					text_no = 0;
				}
			}
			console.log(text_no);
			console.log(text_pos);
		}, 125);
	});

	return (
		<div>
			<div id="landing">
				<h1 id="name">ABHISHEK ADHIKARI</h1>
				<div id="typewriter">
					I'm a&nbsp;
					<span id="typewriter-text">Full-Stack Developer</span>
					<span id="typewriter-end">|</span>
				</div>
			</div>
		</div>
	);
};

export default Landing;
